import React, { Component } from 'react';
import reactHtmlParser from "react-html-parser";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import { withTranslation } from "react-i18next";

export class tabSection extends Component {
  render() {
    const { t } = this.props;

    return (
        <div className="container container-wrapper">
        <h2 className="text-center">{t("Home:hOME40")}</h2>
        <Tabs>
          <TabList>
            <Tab>{t("Home:hOME41")}</Tab>
            <Tab>{t("Home:hOME50")}</Tab>
            <Tab>{t("Home:hOME52")}</Tab>
          </TabList>
          <TabPanel>
            <p>{t("Home:hOME42")}</p>
            <ul>
              <li>{t("Home:hOME43")}</li>
              <li>{t("Home:hOME44")}</li>
              <li>{t("Home:hOME45")}</li>
              <li>{t("Home:hOME46")}</li>
              <li>{t("Home:hOME47")}</li>
              <li>{t("Home:hOME48")}</li>
              <li>{t("Home:hOME49")}</li>
            </ul>
          </TabPanel>
          <TabPanel>
            <p>{reactHtmlParser(t("Home:hOME51"))}</p>
          </TabPanel>
          <TabPanel>
            <p>
              {t("Home:hOME53")}
              <br />
              <ul>
                <li>{t("Home:hOME54")}</li>
                <li>{t("Home:hOME55")}</li>
                <li>{t("Home:hOME56")}</li>
                <li>{t("Home:hOME57")}</li>
                <li>{t("Home:hOME58")}</li>
                <li>{t("Home:hOME59")}</li>
              </ul>
            </p>
          </TabPanel>
        </Tabs>
      </div>
    )
  }
}

export default withTranslation()(tabSection);